    #t_1_2.titulo-segundo.color-acento-contenido
      h2 1.2 Consolidacin y desconsolidacin de mercanca

    p.mb-5 Generalmente, en los almacenes y centros de distribucin, se agrupan (consolidan) y almacenan las mercancas segn el tipo de producto; por ejemplo, donde se utilizan los soportes rgidos horizontales metlicos o de madera, comnmente conocidos como estibas (tcnicamente, pallet o paletas), es necesario una estantera tipo rack o de paletizacin. En caso de que la mercanca sean perfiles, se requiere una estantera tipo cantilver, para mantenerlos bien organizados.

    .row.justify-content-center.mb-4
      .col-lg-8       
        .bloque-texto-d.color-secundario.p-4
          .bloque-texto-d__texto.mb-2
            i.fas.fa-quote-left
            p El xito de la organizacin y agrupacin de la mercanca en un almacn es la forma como se gestionan los productos; se recomienda aplicar la tcnica de gestin japonesa 5S (cinco palabras que comienzan por S), cuyo fundamento es lograr lugares de trabajo mejor organizados, ms limpios, ms ordenados y, por consiguiente, ms productivos. 
            i.fas.fa-quote-right     
        .row.d-none.d-lg-flex
          .col-lg-2
            img(src='@/assets/curso/tema1/ico5.svg', alt='').img85

