        img(src='@/assets/curso/tema1/img9.png', alt='').img350.m-auto

    .cajon.cjn-rosa.p-4.mb-5
      p <strong>Tcnica gestin japonesa 5S</strong>
      p <strong>SEIRI</strong> - Clasificar:  identificar y separar todos aquellos materiales considerados innecesarios y retirarlos del entorno laboral.
      p <strong>SEITON</strong> - Ordenar: establecer la forma en que deben identificarse y ubicar los materiales requeridos, de manera que sea fcil y rpido encontrarlos para la utilizacin.
      p <strong>SEISO</strong> - Limpieza: identificar y eliminar las fuentes de suciedad, asegurando que siempre todo se encuentre en perfecto estado de higiene.
      p <strong>SEIKETSU</strong> - Estandarizacin: el objetivo es lograr distinguir una situacin normal de otra anormal, mediante informacin sencilla, como etiquetas, avisos informativos o sealizacin; y as lograr un control visual a travs de la informacin.
      p <strong>SHITSUKE</strong> - Disciplina: continuar y trabajar permanentemente aplicando las 4s anteriores en todos los sitios de trabajo, de acuerdo con las normas establecidas.
      p.mb-0 Las tres primeras S estn enfocadas al entorno fsico, y las dos ltimas se orientan al comportamiento de las personas.
