    h3 Consolidacin de mercancas 

    p.mb-4 La consolidacin consiste en agrupar o combinar las mercancas sueltas provenientes de las reas de #[i picking], lneas de pedido o destinos y as organizar un solo paquete, con el objetivo de economizar transporte y manipulacin de las mismas.  Esta actividad es desarrollada en la zona de alistamiento (expedicin), preferiblemente cerca del muelle, para optimizar los recursos disponibles; dicha agrupacin consiste en colocar sobre una paleta varios paquetes pequeos, para facilitar la manipulacin, almacenamiento y transporte; en ocasiones, depende del cliente y el destino para organizar la mercanca, como se presenta a continuacin:   

