    p.mb-5 Para realizar la consolidacin de mercancas y objetos postales, se deben tener en cuenta las siguientes actividades:

    .crd.crd--slyder.p-5.mb-5
      SlyderA(tipo="b")
        .row.justify-content-center.align-items-center
          .col-lg-7
            h5 Paletizado
            p Hace referencia a la forma de organizar las mercancas sobre un <i>pallet</i>(estiba) para almacenamiento y transporte, con la finalidad de tener uniformidad y facilitar la manipulacin.
            p El paletizado podr realizarse de forma manual o mecanizado, utilizando toda la superficie de la estiba.    
          .col-auto
            figure
              img(src='@/assets/curso/tema1/img12.png', alt='').img350.m-auto

        .row.justify-content-center.align-items-center
          .col-lg-7
            h5 Retractilado
            p Es el proceso de embalaje en el que una mquina retractiladora envuelve en una pelcula plstica (papel termo film) las mercancas, para quedar adheridas al plstico, evitando as daos o suciedad; quedan pegadas al <i>pallet</i> y se facilita tanto la manipulacin como el transporte.    
          .col-auto
            figure
              img(src='@/assets/curso/tema1/img13.png', alt='').img350.m-auto

        .row.justify-content-center.align-items-center
          .col-lg-7
            h5 Flejado
            p Es la forma de agrupar y asegurar las cajas, mercancas u objetos postales mediante unas tiras plsticas llamadas flejes, que aseguran el pedido que ya est retractilado para evitar la separacin y asegurar mejor los productos, como se observa a continuacin.   
          .col-auto
            figure
              img(src='@/assets/curso/tema1/img14.png', alt='').img350.m-auto

        .row.justify-content-center.align-items-center
          .col-lg-7
            h5 <i>Pool</i> de paletas
            p Es la forma de agrupar y asegurar las cajas, mercancas u objetos postales mediante unas tiras plsticas llamadas flejes, que aseguran el pedido que ya est retractilado para evitar la separacin y asegurar mejor los productos, como se observa a continuacin.   
          .col-auto
