              p.text-white.mb-4 Si es de su inters ampliar la informacin sobre <strong>Entregas paletizadas y mtodos de paletizacin,</strong>revise la presentacin que aparece en el siguiente enlace: 
              a.boton.color-acento-contenido-botones.texto-blanco.float-end(href="https://docplayer.es/3658902-Entregas-paletizadas.html" target="_blank").mb-5.mb-lg-0
                span Ingresar
                i.fas.fa-link 

    p.mb-4 Los <i>pallets</i> de madera son los ms comunes, aunque existen de plstico y material reciclado. Los vehculos de transporte estn diseados para ubicar exactamente un nmero especfico de estibas con las unidades de carga (aproximadamente 33 <i>pallets</i>) y as poder calcular el volumen a transportar. Sin embargo, antes de llevar la carga a los medios de transporte, se requiere consolidar o agrupar las cargas con la ayuda de herramientas y equipos como los que aparecen descritos a continuacin:
    
