    h3 Desconsolidacin de la mercanca
    p La desconsolidacin hace referencia a desagrupar mercancas que previamente haban sido consolidadas y relacionadas en un mismo documento de envo, lo que indica que toda la mercanca que conforma el pedido debe ser entregada a diferentes clientes, segn la carta individual de transporte y nota de entrega.  En otras palabras, consiste en subdividir un pedido grande en otros ms pequeos; esta tarea debe desarrollarse en los centros de distribucin que posean tecnologas para desconsolidar y trasladar mercancas a los diferentes destinatarios.     
   
    p.mb-4 Durante esta operacin, los contenedores que llegan deben ser vaciados de los pedidos entrantes consolidados, para volver a combinarse en los medios de transporte disponibles y ruta programada para los vehculos.  En los centros de distribucin, se logran reducir las existencias del inventario y mejorar la cadena de distribucin y suministro, dado que se realiza una minuciosa organizacin que permiten hacer las entregas en los tiempos acordados.  Un ejemplo claro es el recibimiento de contenedores con mercancas en el puerto, all se desconsolida el pedido, abriendo las puertas, verificando y abriendo el sello de seguridad, para continuar con el vaciado del mismo, permitiendo as que toda la mercanca se clasifique y reorganice para llevarla a los vehculos o medios de transporte (por lo regular terrestre) encargados de la distribucin.    

    .row.justify-content-center.mb-4
      .col-lg-5.mb-4.mb-lg-0
        img(src='@/assets/curso/tema1/img24.png', alt='Texto que describa la imagen')
      .col-lg-7.bg33        
        p.mt-4.mb-0 Para la operacin de desconsolidacin en puerto, se requiere de personal capacitado, gras tipo horquilla y prtico, necesarias para la operacin, los documentos portuarios necesarios para la entrega del pedido, descarga del contenedor y traslado hacia el rea de recepcin temporal en puerto, paletizadoras, embalaje, retractilados, etiquetas, entre otros equipos. 

    p.mb-4 Existen, principalmente, tres tipos de desconsolidacin de contenedores:

    .hr--line
    .row
      .col-auto
        figure(data-aos="zoom-in")
          img(src="@/assets/curso/tema1/img25A.svg", alt="alt").img65
      .col
        h4 Desagrupar o desconsolidar varios embarques 
        p Relacionados en un mismo documento de transporte, carta de porte o equivalente, donde la mercanca est referenciada a diferentes clientes o consignatarios que necesitan una distribucin posterior.   
    .hr--line     
    .row
      .col-auto
        figure(data-aos="zoom-in")
          img(src="@/assets/curso/tema1/img25B.svg", alt="alt").img65
      .col
        h4 Desconsolidar embarques consolidados en un mismo packing 
        p Lista de empaque o lista de carga: inventario de la mercanca que se va a exportar; la cual debe ser clasificada por referencias y as facilitar la ubicacin en el almacn.
    .hr--line     
    .row
      .col-auto
        figure(data-aos="zoom-in")
          img(src="@/assets/curso/tema1/img25C.svg", alt="alt").img65
      .col
        h4 Desconsolidacin de embarques mixtos 
        p Es la combinacin de los dos anteriores y requiere que las mercancas sean distribuidas tanto para stock como para cross-docking (cruzar de un muelle a otro, sin necesidad de almacenar mercancas, como en el caso de objetos postales, pues simplemente llegan y luego se reparten para cada lugar de destino, no se almacenan).
    .hr--line   

    h5.text-center.my-5 En cualquiera de los tres casos, se deben realizar las siguientes actividades:

    .titulo-sexto.color-acento-contenido
      h5 Figura 3.
      span Secuencia de actividades a desarrollar en la desconsolidacin
    
    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="zoom-in")
        img.mt-4(src='@/assets/curso/tema1/img26.svg', alt='Texto que describa la imagen')
        figcaption.mt-3 *SGI:  Sistema de Gestin Informtico (<i>software</i>)      
    
    .cajon.cjn-rosa.p-4.mb-5
      .row.justify-content-center.align-items-center
        .col-auto
          img(src='@/assets/curso/tema1/ico7.svg', alt='Texto que describa la imagen').img85
        .col
          p.mb-0 En el desarrollo de las anteriores actividades, el <strong>agente de carga</strong> juega un papel importante, es la persona encargada de realizar los trmites documentales al momento de recibir el contenedor para la desconsolidacin.     

    .row.justify-content-center.align-items-center
      .col-lg-7        
        p La funcin principal del agente de carga es ayudar a resolver inconvenientes logsticos y de transporte que se lleguen a presentar, tanto para el importador como para el exportador, bien sea en la consolidacin o desconsolidacin de la carga; presta asistencia tcnica en temas fiscales y aduaneros, aseguramiento y recoleccin de productos, e incluso hace seguimiento al tema documental.  En caso de que el contenedor llegue por va martima, el agente de carga debe emitir el <strong>Conocimiento de embarque</strong> y, en caso areo, la <strong>Gua area</Strong>.
      .col-lg-5(data-aos="zoom-in")
        img(src='@/assets/curso/tema1/img27.png', alt='Texto que describa la imagen')


    .titulo-sexto.color-secundario.mt-4.mb-4
      h5 Tabla 3.
      span Caractersticas de documentos requeridos en la desconsolidacin

