        tbody
          tr
            td <strong>Conocimiento de embarque</strong>
            td Documento relacionado con el transporte martimo de mercanca. Obligatorio para operaciones de importacin y exportacin.
            td Dejar constancia de que el buque de carga se hace cargo de la mercanca y la entrega en el puerto acordado.
            td - Contrato de transporte en buques de carga <br>- Registra quin es el destinatario(s). <br>- Recibo que registra la cantidad y estado de la mercanca. <br>- Debe registrar la informacin procesada en la fase de consolidacin.
            td - Datos completos de quien enva y quien recibe. <br>- Nombre y matrcula de los buques de carga. <br>- Puerto de carga y descarga. <br>- Descripcin detallada de la mercanca. <br>- Constancia del precio de la operacin.
          tr
            td <strong>Gua area</strong>
            td Documento que acredita el contrato de transporte internacional va area.
            td Dejar constancia de que el avin o aerolnea se hace cargo de la mercanca y conoce de la existencia de la misma.
            td - Conocida como 
              span.it Air Waybill <br>
              | - Sirve de recibo para el remitente <br>- El transportista se compromete a llevar la mercanca al aeropuerto acordado. <br>- Lo emite la aerolnea o quien haya consolidado la mercanca <br>- El documento debe ser entregado en el pas de destino (importador).
            td - Datos completos del destinatario y de quien enva. <br>- Nmero de vuelo. <br>- Aeropuerto de salida y de llegada. <br>- Valor del flete. <br>- Reflejar la fecha de emisin y detalles de la carga. <br>- Emisin de seis (6) copias para entregar, as: lnea area, destinatario, expedidor, destinatario (firmada al momento de entregar la mercanca), aeropuerto de destino, agente areo.

