    separador

    #t_1_3.titulo-segundo.color-secundario
      h2 1.3 Equipos de movimentacin

    .row.justify-content-center
      .col-lg-8
        p En la gestin del almacn, se requiere revisar qu medios o <STRONG>equipos de manipulacin</STRONG> son necesarios para mover y trasladar mercancas dentro del almacn; mientras que los <STRONG>medios o equipos de almacenamiento</STRONG> hacen referencia a los estantes, cajas, armarios, suelos, silos, contenedores, etc., que <STRONG>sirven para guardar</STRONG> mercancas requeridas posteriormente.
        p Existe un gran nmero de equipos de almacenamiento, pues existen diversos tipos de mercancas, lo que requiere analizar y seleccionar los ms adecuados para los productos.  Incluso, se puede solicitar asesora con el proveedor sobre nuevas tecnologas, materiales o formas de almacenar.  De igual forma, en las actividades de manipulacin de mercancas se pueden utilizar sistemas manuales o automatizados, lo cual requiere recursos humanos y materiales.
      .col-lg-4(data-aos="zoom-in")
        img(src='@/assets/curso/tema1/img30.png', alt='Texto que describa la imagen')

    p.mb-5 En aquellos sistemas manuales y de acuerdo con estndares para manejo de cargas en salud ocupacional, un operario de gnero masculino est en capacidad de trasladar cargas hasta de 25 kg con un recorrido mximo de 30 m, mientras que la mujer puede realizar traslados mximos de 15 kg; un mecanismo de apoyo para superar estas limitaciones y aumentar la capacidad tanto de carga como traslados son los diferentes equipos de manipulacin y movimentacin de cargas. Es importante tener en cuenta aspectos como rotacin de productos y espacio disponible al momento de elegir.  A continuacin, se presenta la clasificacin general de los equipos:

    .titulo-sexto.color-acento-contenido
      h5 Figura 5.
      span Clasificacin equipos de manipulacin y movimentacin   
   
    .row.justify-content-center.bg1.mb-5(data-aos="zoom-in")
      .col-lg-7
        img(src='@/assets/curso/tema1/img31.svg', alt='Texto que describa la imagen')
      .col-lg-5
        img(src='@/assets/curso/tema1/img32.png', alt='Texto que describa la imagen').d-none.d-lg-flex
    
    p.mb-5 <strong>Clasificacin de equipos dinmicos sin traslado:</strong> son equipos que estn fijos al piso o techo de la edificacin y que pueden realizar algn tipo de transporte, como aparece en el siguiente recurso.
    
    .row.justify-content-center.mb-5
      .col-lg-10
        .crd.crd--slyder.p-5
          SlyderA(tipo="b")
            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0   
                p <strong>Transportadora de banda</strong>
                p - Plataforma de goma deslizante, sobre conjunto de rodillos.
                p - Aplica para embalajes de base irregular.
                p - Transporte a granel.
                p - Adecuada para trayectos largos, con curvas y diferentes niveles.            
              .col-lg-5              
                figure
                  img(src='@/assets/curso/tema1/img33.png', alt='Texto que describa la imagen').img350

            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0        
                p <strong>Transportadora de rodillos</strong>
                p - Plataforma de rodillos, sobre rodamientos.
                p - Opera en funcin del tipo de mercanca.
                p - Los productos se desplazan como consecuencia de la gravedad.
              .col-lg-5                       
                figure
                  img(src='@/assets/curso/tema1/img34.png', alt='Texto que describa la imagen').img350

            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0            
                p.margl <strong>Vehculo que comunica varias reas del almacn </strong>
                p - Desplazamiento a altas velocidades.
                p - Movimientos en lnea recta.
                p - Opera con un sistema neumtico.
                p - Precisin en la ubicacin de carga.
                p - Requiere uso de telmetros 
                  span.it (lser).
              .col-lg-5              
                figure
                  img(src='@/assets/curso/tema1/img35.png', alt='Texto que describa la imagen').img350

            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0            
                p <strong>Equipo de transporte fijo al techo </strong>
                p - Permite transporte de mercanca dentro del almacn a diferentes zonas.
                p - Mantiene despejado el suelo.
                p - Requiere techos muy altos en el almacn.
                p - Uso principal en el sector automotriz.
              .col-lg-5              
                figure
                  img(src='@/assets/curso/tema1/img36.png', alt='Texto que describa la imagen').img350

            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0              
                p <strong>Equipo  con desplazamiento  por pasillos estrechos a velocidad alta </strong>
                p - Diseados especialmente para almacenes automatizados.
                p - Operan con dos movimientos: longitudinal sobre 
                  span.it rail 
                  | y vertical para extraer o colocar 
                  span.it pallets.
                p - Desarrollan tareas de picking a gran altura.
              .col-lg-5              
                figure
                  img(src='@/assets/curso/tema1/img37.png', alt='Texto que describa la imagen').img350   
    
    p.mb-5 <strong>Dinmicos con traslado: </strong>aquellos equipos que pueden trasladar mercancas dentro del almacn. Se describen a continuacin:

    .row.justify-content-center.mb-5
      .col-lg-10
        .crd.crd--slyder2.p-5
          SlyderA(tipo="b")
            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0              
                p <strong>Equipo mecnico de transporte</strong>
                p - Sirven de apoyo a otros equipos.
                p - Transportan la carga a distancias cortas.
                p - Levantan distancias muy cortas del suelo.
                p - Apoyan actividades del muelle.   
              .col-lg-5              
                figure
                  img(src='@/assets/curso/tema1/img38.png', alt='Texto que describa la imagen').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0             
                p <strong>Apilador elctrico con mstil telescpico</strong>
                p - Elevacin hasta altura especfica.
                p - Desplazamiento de altura y horizontal.
                p - Permite realizar trabajo de apilado.
                p - Puede ser utilizado en recepcin y expedicin.
                p - No se recomienda para cargas pesadas.            
              .col-lg-5              
                figure
                  img(src='@/assets/curso/tema1/img39.png', alt='Texto que describa la imagen').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0           
                p <strong>Carretilla retrctil, contrabalanceada, de toma lateral (bi o trilateral) </strong>
                p - Movimientos vertical y horizontal.
                p - Levanta cargas hasta 10 m.
                p - Necesita pasillos para operar.
                p - Recorridos medios-largos.
                p - Uso en ubicacin y apilado de mercanca. 
              .col-lg-5
                <br><br>
                figure
                  img(src='@/assets/curso/tema1/img40.png', alt='Texto que describa la imagen').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-lg-7.mb-4.mb-lg-0 
                p <strong>Automatic Guided Vehicles </strong>
                p - No requieren conductor (inteligentes).
                p - Ordenador que comunica con PC.
                p - Aplica diferentes tcnicas de gua: hilo conductor a poca profundidad del suelo; uso banda magntica sobre el suelo.
                p - Sigue un trayecto orientado por demarcacin.
              .col-lg-5              
                figure
                  img(src='@/assets/curso/tema1/img41.png', alt='Texto que describa la imagen').img350.m-auto 
    
    p.mb-5 <strong>Estticos:</strong>  se utilizan para almacenar mercanca a granel, tienen un orificio de entrada y uno de salida.  Son muy utilizados para manejar semillas y minerales, pueden ser cilndricos o poligonales. Los ms comunes son los silos de almacenamiento. 

    .row.justify-content-center.mb-5
      .col-lg-10
        .cta--yellow
          .bubble--right
          .dots--right
          .row.align-items-center
            .col-auto.mb-4.mb-lg-0
              figure
                img(src="@/assets/curso/llamados/cta_01.png", alt="alt").img385.m-auto
            .col-12.col-lg.px-3.px-5.px-lg-0.pt-0.pt-lg-5.txt-mg--right
              p <strong>Material de apoyo</strong>
              p.mt-4 En caso de ser necesario ampliar la informacin, ingrese a Sistemas de Biblioteca Sena: Flamarique, S. (2017). Gestin de operaciones de almacenaje. Marge Books.
