        img.all(src='@/assets/curso/tema1/ico8.svg', alt='Texto que describa la imagen').img100.m-auto
      .col-lg-9
        p Existen principalmente dos categoras de almacenes: manuales y automticos.  Los <strong>manuales</strong> bsicamente son operados solo por el talento humano, quienes aumentan en cantidad a medida que las operaciones y movimientos son mayores; se apoyan en equipos muy sencillos, como carretillas, puente-gra, transpals, etc.  De otro lado, los almacenes <strong>automatizados</strong> tienen mayor complejidad tcnica, pues son operados por un conjunto de recursos mecnicos, dinmicos, por gravedad o elctricos, por tanto, requieren menos personal y una mayor efectividad en los sistemas de gestin informtica o de radiofrecuencia.  A continuacin, se realiza la descripcin de las diferentes clases de sistemas de almacenamiento:
    
    .row.justify-content-center.mb-5
      .col-lg-10
        .crd.crd--slyder3.p-5
          SlyderA(tipo="b")
            .row
              .col-lg-7                
                p <strong>Sistema de bloque</strong>
                p - Organiza la mercanca en bloques homogneos. 
                p - Puede ser al aire libre, no requiere estantes.
                p - Uso para mercanca que viene en grandes cantidades.               
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema1/img42.png', alt='Texto que describa la imagen').img385.m-auto
            .row
              .col-lg-7
                p <strong>Sistema convencional</strong>
                p - Almacenamiento de productos combinando pallets y estantera.
                p - Requiere la utilizacin de un soporte intermedio para separar la mercanca.
                p - Se aprovecha el uso del espacio del almacn.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema1/img43.png', alt='Texto que describa la imagen').img385.m-auto
            .row
              .col-lg-7
                p <strong>Sistema compacto </strong>
                p - Almacenamiento de mercancas en estantes que permitan el paso de carretillas en los pasillos.
                p Permite ocupacin de la superficie y del volumen del almacn.
                p Requiere poco mantenimiento, centrado en infraestructura y estanteras.  
              .col-lg-5                
                figure
                  img(src='@/assets/curso/tema1/img44.png', alt='Texto que describa la imagen').img385.m-auto
            .row
              .col-lg-7
                p <strong>Sistema dinmico </strong>
                p - Recomendado para mercancas que requieran una rotacin exacta.
                p - Se compone de estanteras metlicas, que forman caminos con rodillos (inclinada o de forma horizontal).
                p - ptimo aprovechamiento de la superficie.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema1/img45.png', alt='Texto que describa la imagen').img385.m-auto
            .row
              .col-lg-7
                p <strong>Sistema mvil </strong>
                p - Requiere la instalacin de estantes sobre bases mviles, separadas unas de otras.
                p - El desplazamiento se realiza en funcin del pasillo principal y sobre el cual se requiera acceder.
                p - Muy utilizado para almacenar mercanca pequea y heterognea, por ejemplo, documentos o libros.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema1/img46.png', alt='Texto que describa la imagen').img385.m-auto

            .row
              .col-lg-7
                p <strong>Sistema de </strong>
                  span.it <strong>Shuttle OSR </strong>
                p - Preparacin de pedidos semiautomtica; basado en el principio de mercanca al hombre.
                p - Los productos se desplazan desde las estanteras con ayuda de los <i>shuttles</i> (lanzaderas), hasta la zona de expedicin.
                p - Un elevador se encarga del transporte vertical.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema1/img47.png', alt='Texto que describa la imagen').img385.m-auto
    
            .row
              .col-lg-7
                p <strong>En estanteras </strong>
                p - Almacenaje de unidades lista para posterior uso.
                p - Los pasillos se deben adaptar a la maquinaria utilizada.
                p - Ancho estandarizado de acuerdo con medidas de pallets.
                p - Estanteras compacta, dinmica, <i>rack</i> para <i>picking</i>, estantera para bidones y ruedas. 
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema1/img48.png', alt='Texto que describa la imagen').img385.m-auto

            .row
              .col-lg-7
                p <strong>Estanteras Cantilever </strong>
                p - Almacenaje de unidades de gran longitud.
                p - Estructura vertical fijada al suelo, con ganchos o agarres para colocar los productos, como perfiles metlicos (en forma de U o T).
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema1/img49.png', alt='Texto que describa la imagen').img385.m-auto
    
            .row
              .col-lg-7
                p <strong>Armarios </strong>
                p - Especie de mueble tipo <i>closet</i> para almacenar mercancas pequeas.
                p - Ofrecen seguridad, pues se puede almacenar bajo llave.
                p - Existen estructuras con vidrios de seguridad para exhibir los productos (tipo vitrina).
              .col-md-5
                figure
                  img(src='@/assets/curso/tema1/img50.png', alt='Texto que describa la imagen').img385.m-auto
    
            .row
              .col-lg-7
                p <strong>Dispensadores </strong>
                p - Tolvas o depsitos verticales, opera como dosificador y deja caer por gravedad la cantidad necesaria.
                p - El producto lo recibe una banda transportadora que lleva el producto.
                p - Muy utilizado en la industria farmacutica.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema1/img51.png', alt='Texto que describa la imagen').img385.m-auto
    
            .row
              .col-lg-7
                p <strong>Autoportantes </strong>
                p - Integracin de la propia estructura del edificio al almacenamiento.
                p - Soportan cargas almacenadas hasta 35 m de altura.
                p - Utilizacin principalmente por almacenes automatizados.
                p - Nivel de codificacin e informacin debe ser elevado.
              .col-md-5
                figure
                  img(src='@/assets/curso/tema1/img52.png', alt='Texto que describa la imagen').img385.m-auto
    
            .row
              .col-lg-7
                p <strong>Carruseles </strong>
                p - Almacenamiento vertical (automatizado).  Desplazamiento de la carga hacia el manipulador.
                p - Estanteras en serie para almacenar piezas pequeas; giran automticamente de manera horizontal.
                p - Producto controlado por el <i>software</i> de gestin y control de almacn.
              .col-md-5                
                figure
                  img(src='@/assets/curso/tema1/img53.png', alt='Texto que describa la imagen').img385.m-auto

    .row.justify-content-center.mb-5
      .col-lg-10
        .cta--yellow
          .bubble--right
          .dots--right
          .row.align-items-center
            .col-auto.mb-4.mb-lg-0
              figure
                img(src="@/assets/curso/llamados/cta_05.png", alt="alt").img385.m-auto
            .col-12.col-lg.px-3.px-5.px-lg-0.pt-0.pt-lg-5.txt-mg--right
              h5 Material de apoyo
              p.mb-0 En caso de ser necesario ampliar la informacin, ingrese a Sistemas de Biblioteca Sena:
              p Flamarique, S. (2017). Gestin de operaciones de almacenaje. Marge Books. 
