      .col-lg
        p A lo largo de la cadena de suministro, en logstica, se genera gran cantidad de informacin, que para este caso se denominan datos, los cuales se han multiplicado y revolucionado las tecnologas en los ltimos tiempos.  En busca de una solucin para el control y gestin de la informacin, se ha recurrido a sistemas de informacin que logren generar valor a los datos para la toma de decisiones. Hoy en da, se conoce como tecnologa 4.0 y suministros inteligentes <i>Big Data</i>.
    
    h4.mt-4 Logstica 4.0
