    h4 Fuentes de informacin

    p.mb-5 Debido al volumen de datos que maneja la cadena de suministro, es imposible analizar y tomar decisiones con mtodos tradicionales, por lo que se requiere organizar las diferentes fuentes para el suministro de informacin:    

